import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { MpcComponent } from './mpc/mpc.component';
import { MpcsComponent } from './mpcs/mpcs.component';
import { BzcComponent } from './bzc/bzc.component';
import { BcomgenComponent } from './bcomgen/bcomgen.component';
import { BcomcomComponent } from './bcomcom/bcomcom.component';
import { BaeppComponent } from './baepp/baepp.component';
import { BahppComponent } from './bahpp/bahpp.component';
import { RouterModule,Routes } from "@angular/router";
const routing:Routes=[
  {path:'MPC',component:MpcComponent},
  {path:'MPCs',component:MpcsComponent},
  {path:'BZC',component: BzcComponent},
  {path:'BcomGen',component:BcomgenComponent},
  {path:'BcomComp',component:BcomcomComponent},
  {path:'BAEpp',component:BaeppComponent},
  {path:'BAHpp',component:BahppComponent},
]
@NgModule({
  declarations: [MpcComponent, MpcsComponent, BzcComponent, BcomgenComponent, BcomcomComponent, BaeppComponent, BahppComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routing)
  ]
})
export class CoursesModule { }
